igente Aplicable. Esta cancelación implica la supresión total o parcial de la\n            información personal de acuerdo con lo solicitado por el titular en los registros,\n            archivos, bases de datos o tratamientos realizados por el responsable, previo bloqueo."),_c('br'),_c('br'),_vm._v("\n\n            Si usted quiere ejercer sus derechos ARCO respecto a sus datos personales que estén en\n            posesión de WOONKLY lo podrá realizar enviando la solicitud para ejercer sus derechos\n            ARCO, a nuestro Departamento de Datos Personales en nuestro correo electrónico:\n            shuberth@woonkly.com"),_c('br'),_c('br'),_vm._v("\n\n            Usted podrá obtener el formato para ejercer sus derechos ARCO dando clic aquí o bien a\n            través de un escrito enviándolo a nuestro domicilio de WOONKLY señalado en el numeral\n            XIII DATOS DE CONTACTO."),_c('br'),_c('br'),_vm._v("\n\n            Para responder a su solicitud WOONKLY tendrá un plazo de veinte (20) días hábiles\n            contados a partir de la fecha en que recibió la solicitud para responder. Si la\n            solicitud es procedente, se hará efectiva dentro de los quince (15) días hábiles\n            siguientes a la fecha en que se le haya comunicado la respuesta anterior. Estos plazos\n            podrán ser ampliados por una sola vez y por un periodo igual, cuando las circunstancias\n            del caso lo ameriten. La entrega de información procederá previa acreditación de su\n            identidad o de su representante legal, según corresponda. Si usted deseara revocar el\n            consentimiento otorgado para el tratamiento de datos personales por parte del WOONKLY lo\n            deberá informar a ésta por escrito de la misma manera."),_c('br'),_c('br'),_vm._v("\n\n            Al momento en que usted haga la revocación del uso de sus datos personales existe la\n            posibilidad de que Woonkly no pueda seguir prestándole sus servicios o productos